.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px 4px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  font-family: cursive;
  font-weight: lighter;
  cursor: pointer;
}

.site-layout .site-layout-background {
  background: #fff;
}

.ant-layout-has-sider {
  height: 100%;
}

.ant-layout-sider-children {
  height: 100vh !important;
}

.error-screen {
  border: 1px solid red;
  padding: 12px;
  background: lightyellow;
}
.count-number {
  font-size: 32px;
  font-weight: lighter;
}
.count-card {
  width: 16vw;
  background-color: #1890ff !important;
}
.card-content > h2,h1  {
  color: #fff !important;
}
.card-content > h2  {
  text-align: left;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 1s linear, visibility 2s linear;
}
